<template>
  <div>
    <div id="top">
      <div>
        <span>荐书者</span>
        <el-input
          v-model="recommender"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <span>书名</span>
        <el-input
          v-model="name"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <span style="margin-right: 10px">荐书日期</span>
        <el-date-picker
          v-model="dateValue"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          size="mini"
        >
        </el-date-picker>

        <span style="margin-left: 20px">是否上架</span>
        <el-select
          v-model="status"
          placeholder="请选择"
          class="search"
          size="mini"
        >
          <el-option label="是" value="on"></el-option>
          <el-option label="否" value="off"></el-option>
        </el-select>

        <el-button-group style="margin-left: 20px">
          <el-button type="primary" size="mini" @click="search">搜索</el-button>
          <el-button size="mini" @click="reset">重置</el-button>
        </el-button-group>
      </div>
    </div>

    <!-- <div style="margin-bottom:10px;">
        <span
          >已选择<span style="color: rgb(0, 161, 145); padding: 0 5px">{{
            tableSelection.length
          }}</span
          >项</span
        >
        <el-link
          class="table_link"
          type="danger"
          v-if="tableSelection.length"
          :underline="false"
          @click="handleMultiDelete"
          style="margin: -5px 10px 0px 10px"
          >批量删除</el-link
        >
      </div> -->
    <el-table
      v-loading="loading"
      ref="multipleTable"
      :data="tableData"
      size="small"
      border
      :height="height - 180"
      style="width: 100%; text-align: center; line-height: 18px"
    >
      <!-- <el-table-column
          type="selection"
          width="55"
          align="center"
        ></el-table-column> -->
      <el-table-column
        label="书名"
        align="center"
        prop="name"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column label="作者" align="center" prop="author">
      </el-table-column>
      <el-table-column
        label="内容"
        align="center"
        prop="content"
      >
      <template slot-scope="{ row }">
          <el-button type="text" @click="showInfo(row)">查看</el-button>
        </template>
      </el-table-column>
      <el-table-column label="推荐人" align="center">
        <template slot-scope="{ row }">
          {{ row.user != null ? row.user.username : "无" }}
        </template>
      </el-table-column>
      <el-table-column label="荐书日期" align="center" width="150">
      </el-table-column>

      <el-table-column
        label="是否上架 "
        align="center"
        prop="electronicTag"
        width="100"
        show-overflow-tooltip
      ></el-table-column>

      <el-table-column label="操作" align="center" width="200">
        <template slot-scope="{ row }">
        
          <el-switch
            v-model="row.status"
            active-text="采纳"
            inactive-text="不采纳"
          >
          </el-switch>
          <div style="margin-top:10px">
            <el-link type="primary" style="margin-right: 10px">回复</el-link>
            <el-link type="primary" style="margin-left: 10px"  @click="handleDelete(row)">删除</el-link>
       
          </div>
        
        </template>
      </el-table-column>
      <template slot="empty">
        <div>
          <i
            class="iconfont iconzanwuziyuan"
            style="font-size: 40px; color: #8c8c8c"
          >
            <p style="font-size: 14px; margin-top: -30px">暂无资源</p>
          </i>
        </div>
      </template>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 150, 200]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      style="float: right; margin-top: 20px"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: "data_cop",
  components: {},
  props: {
    height: {
      type: Number,
    },
  },
  data() {
    return {
      loading: false,
      currentPage: 1, //当前页码
      pageSize: 20,
      total: 0,
      tableData: [],
      recommender: "",
      name: "",
      dateValue: "",
      status: "",
    };
  },
  created() {},
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.$reqGet("/recommend/allData").then((res) => {
        if (res.code == 200) {
          this.tableData = res.data;
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    handleDelete(row) {
      this.$confirm("确定删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        closeOnClickModal: false,
      }).then(() => {
        let id = row.id;
        this.$reqDel("/recommend/delData", {
          id,
        }).then((res) => {
          if (res.code == 200) {
            this.$message.success("操作成功");
            this.getData();
          } else {
            this.$message.error(res.msg);
          }
        });
      });
    },

    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },
    handleSelectionChange(val) {
      console.log(val);
    },
    search() {
      console.log(this.recommender, this.name, this.dateValue, this.status);
    },
    reset() {
      this.recommender = "";
      this.name = "";
      this.dateValue = "";
      this.status = "";
    },
    showInfo(row) {
      this.$alert(row.content, "内容", {
        confirmButtonText: "确定",
      });
    },
  },
};
</script>
<style scoped>
#top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  margin-top: 10px;
  font-size: 14px;
  color: #595959;
}
.search {
  width: 140px;
  margin-left: 10px;
  margin-right: 10px;
}
</style>
