<template>
  <el-form :model="form" class="demo-form-inline">
    <el-form-item label="回复文案：">
      <el-input
        type="textarea"
        :rows="8"
        placeholder="请输入内容"
        v-model="form.content"
      >
      </el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit">提交</el-button>
    </el-form-item>
  </el-form>
</template>
<script>
export default {
  name: "reply_form",
  data() {
    return {
      form: {
        content: "",
      },
      id:null
    };
  },
  mounted(){
    this.getData()
  },
  methods: {
    getData(){
      this.$reqGet("/recommend/reply").then(res=>{
          if(res.code==200){
           this.form.content=res.data.content
           this.id=res.data.id
          }else{
            this.$message.error(res.msg);
          }
      })
    },
    onSubmit() {
      let method='$reqPost'
      if(this.id)method='$reqPut'
      
      this[method]("/recommend/manageReply",{
       content:this.form.content,
       id:this.id
      }).then(res=>{
          if(res.code==200){
            this.$message.success('提交成功')
            this.getData()
            
          }else{
            this.$message.error(res.msg);
          }
      })
   
    },
  },
};
</script>
